.widget-area {
	margin-top: 1.5em;
        margin-right: 2em;
}

.widget {
	margin: 0;
	padding: 0;
	font-family: $font__sans;
	
	ul,
	ol {		
		margin: 0;
		list-style-type: none;

		li {
			display: block;
			line-height: 2em;

			ul,
			ol {
				margin-left: .5em;

				@media screen and (min-width: 30em) {
					margin-left: 1em;
				}
			}
			
		}
	}
	
	a {
		font-weight: 600;
		text-decoration: none;
		
		&:hover,
		&:focus {
			border-bottom: 3px solid $color__link-hover;
		}
	}
	
	/* Make sure select elements fit in widgets. */
	select {
		width: 100%;
		height: 2.5em;
		padding: 10px;
		font-family: $font__sans;
		border: 1px solid #ccc;
	}
	
}

.widget-title {
	margin-top: 0;
        margin-bottom: .5em;
        font-size: 1.4em;
        text-align: center;
}

/* Archive and Categories widgets */
.widget_archive li {
	color: #7D7D7D;
}

.widget_categories .children {
	padding-top: 0;
}

/* Search widget. */
.widget_search .search-submit {
	display: block;
}

/* Pages and Menu widgets */
.widget_pages .children,
.widget_nav_menu .sub-menu {
	padding-top: 0;
	padding-bottom: .5em;
}

.fd-video {
    max-width: 150px;
}

.widget_pages,
.widget_nav_menu {
	
	a {
		display: block;
		padding: 1em 0;
		text-decoration: none;
		border-bottom: 1px dashed hsl(0, 0%, 50%);
		
		&:hover,
		&:focus {
			text-decoration: none;
			border-bottom: 1px dashed hsl(0, 0%, 50%);
		}
	}
	
	li {
		padding: 0;

	}
	
}


/* Meta widget */
.widget_meta li {
	padding: 0;
}

/* Recent comments */
.widget_recent_comments {

	li {
		line-height: 1.3em;
		padding-bottom: .5em;
	}
	
	a:hover,
	a:focus {
		padding-left: 0;
	}
	
}

/* RSS widget */
.widget_rss li {
	margin-bottom: 1em;
}
.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rssSummary {
	padding: .5em 0;
	font-family: $font__serif;
	font-size: .8em;
    line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;
	
	caption {
		padding: .5em 0;
		font-size: 1em;
		font-weight: 500;
		text-align: left;
	}
	
	thead {
		background: transparent;
		color: black;
		
		th {
			border-bottom-width: 2px;
		}
	}
	
	td {
		padding: .2em;
		font-size: .8em;
		text-align: center;
		background: white;
		border: 1px solid hsl(0, 0%, 80%);
	}
	
	th {
		border: none;
		text-align: center;
	}
	
	.pad {
		background: #eaeaea;
	}
	
	a {
		font-weight: 900;
		text-decoration: none;
	}
	
}

/* Text widget */
.textwidget {
	font-size: 75%;
	line-height: 1.2em;
        color: $color__text-ins;
}

/*side bar widget styles*/
.page-widget {
    .widget_nav_menu {
        font-size: .85em;
        text-shadow: 1px 1px black;
        background-color: $color__background-ins;
    }
}

.socialmedia-buttons {
    display: grid;
    grid-template-columns: repeat(3, 33% [col-start]);
}

.page-widget {
    @media screen and (max-width: $query__small){
    display: none;
    }
    
    @media screen and (min-width: $query__small) {
                margin: auto;
                background-color: $color__trans-asisde-background;
    }
    
    @media screen and (min-width: $query__medium) {
                max-width: 400px;
                display: block;
                float: right;
                background-color: $color__trans-background;
    }
}

#secondary {
    border-radius: 5px;
    font-family: $font__serif;
    font-size: .75em;
}

/* Footer widget stylings */

.footer-widget {
        display: grid;
	margin: 2em 0 0;
	padding: 1em 0;
	background-color: $color__skin;
        font-size: .75em;
        grid-template-columns: repeat(3, 33% [col-start]);
        
        ul {
            margin-bottom: 0;
            list-style-type: none;
            text-align: center;
            padding-left: 0px;
            margin-left: 0px;
        }
}
	
@media screen and (min-width: $query__small) {
        padding: 2em 1em;
}

.widget {
        flex: 5;
        margin: 0 1em 1em;
        padding-top: 1em;
        border-radius: 5px;

        .original-title {
                color: hsla(0, 0%, 100%, .5);
        }
}

.widget,
.widget-title,
.widget_calendar thead,
.rss-date, 
.widget_rss cite {
        border-radius: 5px;
        color: $color__background-ins;
        text-shadow: .5px .5px black;
        
        @media screen and (min-width: $query__small) {
                //display: contents;
                color: $color__text-ins;
                font-size: 1.3875em;
    }
}

.widget_calendar td,
.widget a,
.widget_calendar a {
        color: $color__link;
        text-shadow: none;
}

@media screen and (min-width: $query__medium) {
        /* Use flex to create a responsive layout */
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        /* If only one widget is displayed, make it full width. */
        .widget:only-of-type {
                width: 100%;
        }

        /* If 2 or 3 widgets are displayed, make the second one twice as wide. */
        .widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2) { 
                flex: 2;
        }

        /* If 4 or more widgets are displayed, show three across when available. */
        .widget:nth-last-child(n+4), 
        .widget:nth-last-child(n+4) ~ .widget { 
                width: auto;
                flex: 1 1 30%;
        }
}
	

